import { AppProvider } from "./AppContext";
import { Garage } from "./components/garage/Garage";
import { Impound } from "./components/impound/Impound";
import ImpoundForm from "./components/impound-form/ImpoundForm";
import CreatePrivateGarage from "./components/create-private-garage-form/CreatePrivateGarageForm";
import VehiclePlateForm from "./components/vehicle-plate-form/VehiclePlateForm";
import DevControlPanel from "./components/dev-control-panel/DevControlPanel";
import "./app.css";

function App() {
  const isDev =
    process.env.NODE_ENV === "development" &&
    process.env.REACT_APP_IN_GAME_DEV !== "1";

  return (
    <div className={isDev ? "dev-bg" : ""}>
      <AppProvider>
        {isDev && <DevControlPanel />}
        <Garage />
        <Impound />
        <ImpoundForm />
        <CreatePrivateGarage />
        <VehiclePlateForm />
      </AppProvider>
    </div>
  );
}

export default App;
