import { useEffect, useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import JgBadge from "../jg-badge/JgBadge";

const CreatePrivateGarage = () => {
  const { event, onCloseModal } = useAppContext();
  const [showImpoundForm, setShowImpoundForm] = useState(false);
  const [allPlayers, setAllPlayers] = useState([]);
  const [garageName, setGarageName] = useState("");
  const [playerId, setPlayerId] = useState("");
  const [locale, setLocale] = useState({});

  useEffect(() => {
    if (event.type === "showPrivGarageCreator") {
      setShowImpoundForm(true);
      setAllPlayers(event.allPlayers);
      setGarageName("");
      setPlayerId(event.allPlayers.length ? event.allPlayers[0].id : "");
      setLocale(event.locale);
    }
  }, [event]);

  const onFormSubmit = async (e) => {
    e.preventDefault();

    if (!playerId) return false;

    await fetch(`https://jg-advancedgarages/createPrivGarage`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        garageName,
        playerId: parseInt(playerId),
      }),
    })
      .then((r) => r.json())
      .then((r) => {
        if (!r.error) onHide();
      });
  };

  const onHide = () => {
    setShowImpoundForm(false);
    onCloseModal();
  };

  return (
    <Modal show={showImpoundForm} onHide={onHide} backdrop={false} centered>
      <Form onSubmit={onFormSubmit}>
        <Modal.Header closeButton>
          <Modal.Title as="h5">{locale.createPrivateGarage}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="alert alert-info">
            <strong>
              <i className="bi-exclamation-triangle me-2" />
              {locale.pgAlertHeadsUp}
            </strong>{" "}
            {locale.pgAlertText}
          </div>

          <div className="mb-3">
            <Form.Label>{locale.garageName}</Form.Label>
            <Form.Control
              type="text"
              value={garageName}
              onChange={(e) => setGarageName(e.target.value)}
              required
            />
          </div>
          <div className="mb-3">
            <Form.Label>{locale.player}</Form.Label>
            <Form.Select
              value={playerId}
              onChange={(e) => setPlayerId(e.target.value)}
              required
            >
              {allPlayers.map(({ id, name }) => (
                <option key={id} value={id}>
                  {name} (#{id})
                </option>
              ))}
            </Form.Select>
          </div>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-between text-muted">
          <Button variant="success" type="submit">
            {locale.createPrivateGarage}
          </Button>
          <JgBadge />
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default CreatePrivateGarage;
