import { createContext, useContext, useEffect, useState } from "react";

const AppContext = createContext();

const AppProvider = ({ children }) => {
  const [event, setEvent] = useState(false);

  useEffect(() => {
    const handleEventListener = ({ data }) => setEvent(data);

    window.addEventListener("message", handleEventListener);
    return () => window.removeEventListener("message", handleEventListener);
  }, []);

  const onCloseModal = async () => {
    await fetch(`https://jg-advancedgarages/close`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
    });

    setEvent(false);
  };

  return (
    <AppContext.Provider value={{ event, onCloseModal }}>
      {children}
    </AppContext.Provider>
  );
};

const useAppContext = () => useContext(AppContext);

export { AppProvider, useAppContext };
