import { Accordion, Badge } from "react-bootstrap";
import { useGarageContext } from "../Garage";

const VehicleHeader = ({ vehicle }) => {
  const { vehicleLabel, plate, impound, garage_id: vehGarageId } = vehicle;
  const { garageId, garageVariant, locale } = useGarageContext();

  return (
    <Accordion.Header>
      <div className="d-flex flex-fill justify-content-between">
        <div>
          <span className="me-2">{vehicleLabel}</span>
          <Badge bg="dark" text="warning" style={{ fontFamily: "monospace" }}>
            {plate}
          </Badge>
        </div>
        <div className="me-3">
          {impound ? (
            <Badge bg="danger">{locale.impound}</Badge>
          ) : (
            ["public", "house"].includes(garageVariant) && (
              <Badge
                bg={vehGarageId === garageId ? "primary" : "dark"}
                text={vehGarageId === garageId ? "white" : "muted"}
              >
                {vehGarageId}
              </Badge>
            )
          )}
        </div>
      </div>
    </Accordion.Header>
  );
};

export default VehicleHeader;
